import React from 'react';
import cross from '../cross.png'

import './Backdrop.css';

const backdrop = (props) => (
    props.show ? <div className={props.className} onClick={props.clicked}><img src={cross} className='z-max fr' height='50px' width='50px'/></div> : null
);

export default backdrop;


