import React from 'react';
import './userlist.css'
const UsersList=(props)=>{
	return(
		
		props.users.map((user,i)=>{
			if(user.verified===true){return(
						<div key={user.id} className='userlist f4-l f5-m f6-ns f7 tc ba v-mid pt4 h3 grow pointer w-100 fr mr0 flex flex-column bb' onClick={()=>{props.userSelect(user)}}>
						{user.name}({user.year}, {user.branch})
						</div>
			)}
			else{null}})
	
		)}
export default UsersList;


